import settingApi from '@/lin/model/setting';
import CustomPagination from '@/components/CustomPagination.vue';
export default {
  name: 'ShareSetting',
  components: {
    CustomPagination
  },
  data() {
    return {
      form: {
        shareCode: ''
      },
      bindShareCodeInput: '',
      shareList: [],
      loading: false,
      page_number: 1,
      page_size: 15,
      // 默认每页15条
      count: 0,
      totalPages: 0,
      // 新增总页数
      isBindCodeAvailable: false
    };
  },
  async created() {
    await this.loadInitialData(); // 加载初始数据
  },

  methods: {
    async loadInitialData() {
      await this.getShareCode(); // 获取分享代码
      if (this.form.shareCode) {
        // 如果分享代码存在，则查询分享列表
        await this.fetchShareList();
      }
      const bindCode = await settingApi.queryBindCode(); // 调用接口获取绑定代码
      this.bindShareCodeInput = bindCode; // 填充到输入框
    },

    async fetchShareList() {
      if (!this.form.shareCode) {
        // 检查分享代码是否存在
        this.$message.warning('请先创建自己的分享代码'); // 提示用户
        return; // 终止方法执行
      }

      this.loading = true;
      try {
        const response = await settingApi.getPagedList({
          ShareCode: this.form.shareCode,
          // 使用我的分享代码
          Count: this.page_size,
          Page: this.page_number - 1
        });
        this.shareList = response.items; // 更新分享列表
        this.count = response.count; // 更新总条目数
        this.totalPages = Math.ceil(this.count / this.page_size); // 计算总页数

        // 确保当前页数不超过总页数
        if (this.page_number > this.totalPages) {
          this.page_number = this.totalPages;
        }
      } catch (error) {
        this.$message.error('加载分享列表失败，请重试。'); // 提示用户
      } finally {
        this.loading = false; // 确保加载状态被重置
      }
    },

    async createShareCode() {
      this.form.shareCode = await settingApi.createShareCode();
      this.$message.success(`创建的分享代码: ${code}`);
      await this.loadInitialData(); // 刷新所有数据
    },

    async getShareCode() {
      const code = await settingApi.getCode();
      this.form.shareCode = code;
    },
    async bindShareCode() {
      const response = await settingApi.bindCode(this.bindShareCodeInput);
      this.$message.success(response);
      await this.fetchShareList(); // 刷新分享列表
    },

    handleCurrentChange(newPage) {
      this.page_number = newPage;
      this.fetchShareList(); // 刷新列表
    }
  }
};